import React, { Component } from 'react';
import {
    withRouter
} from 'react-router-dom'
import './UserRecharge.scss';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Rate,Modal } from 'antd';
import { is, fromJS } from 'immutable';

function formatPayType(str){
    if(!str) return;
    str += '';
    let tmp = '';
    try {
        switch (str){
            case 'WX':
                tmp = '微信';
                break;
            case 'AP':
                tmp = '支付宝';
                break;
            case 'CARD':
                tmp = '银行卡';
                break;
            case '1':
                tmp = '处理中';
                break;
            case '2':
                tmp = '交易中';
                break;
            case '3':
                tmp = '取消';
                break;
            case '4':
                tmp = '完成';
                break;
            default:
                tmp = '未知';
                break;
        }
        return tmp
    }catch(err){
        console.error(err)
    }
}
class UserRecharge extends Component{
    static propTypes = {
        qrcode: PropTypes.string.isRequired,
        isShow: PropTypes.bool.isRequired,
        onCancel: PropTypes.func.isRequired,
        data: PropTypes.object.isRequired
    };
    state = {
        rechargeType: '',
        nickName: '',
        account: '',
        tradeStatus: ''
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    };
    componentDidMount(){
        const {data} = this.props;
        const { payType,tradeStatus } = data;
        this.switchPayTypes(payType);
        this.switchStatus(tradeStatus)
    };
    switchPayTypes = (payType) => {
        payType += '';
        switch (payType){
            case 'WX':
                this.setState({
                    rechargeType: '微信',
                    nickName: '微信昵称:',
                    account: '微信账户:',
                    status: ''
                });
                break;
            case 'AP':
                this.setState({
                    rechargeType: '支付宝',
                    nickName: '支付宝昵称:',
                    account: '支付宝账户:',
                });
                break;
            case 'CARD':
                this.setState({
                    rechargeType: '银行卡',
                    nickName: '姓名:',
                    account: '卡号:',
                });
                break;
            default:
                alert('请重新登录');
                break;
        }
    };
    switchStatus = (tradeStatus) => {
        tradeStatus += '';
        switch (tradeStatus){
            case '1':
                this.setState({
                    tradeStatus: '处理中'
                });
                break;
            case '2':
                this.setState({
                    tradeStatus: '交易中'
                });
                break;
            case '3':
                this.setState({
                    tradeStatus: '取消'
                });
                break;
            case '4':
                this.setState({
                    tradeStatus: '完成'
                });
                break;
            default:
                alert('请重新登录');
                break;
        }
    };
    render(){
        const { qrcode,onCancel,isShow,data } = this.props;
        const { rechargeType,nickName,account,tradeStatus } = this.state;
        console.log(data)
        return(
            <div className="record-user_recharge">
                <Modal
                    visible={isShow}
                    wrapClassName="vertical-center-modal"
                    title="用户充值"
                    width={500}
                    destroyOnClose={true}
                    onCancel={onCancel}
                    footer={null}
                >
                    <div className="order_detail">
                        <div className="top">
                            <div className="qrcode">
                                <img src={qrcode} alt=""/>
                            </div>
                            <div className="time">
                                <span>时间:</span>
                                <span>{data.createTime}</span>
                            </div>
                            <div className="account">
                                <span>分号:</span>
                                <span>zhangsan</span>
                            </div>
                            <div className="nick_name">
                                <span>{nickName}</span>
                                <span>张三</span>
                            </div>
                            <div className="pay_account">
                                <span>{account}</span>
                                <span>dsds</span>
                            </div>
                            <div className="order_no-evluation">
                                <div className="order_no">
                                    <span>订单号:</span>
                                    <span>{data.orderNo}</span>
                                </div>
                                <div className="evluation">
                                    <span>评价:</span>
                                    <span>
                                    <Rate allowHalf disabled defaultValue={2.5} />
                                </span>
                                </div>
                            </div>
                        </div>
                        <div className="bottom">
                            <div>
                                <div className="amount">
                                    <span>交易量:</span>
                                    <span>{data.amount}</span>
                                    <small>KGT</small>
                                </div>
                                <div className="pay_type">
                                    <span>方式:</span>
                                    <span>{rechargeType}</span>
                                </div>
                            </div>
                            <div>
                                <div className="price">
                                    <span>KGT价格:</span>
                                    <span>1.10</span>
                                    <small>CNY/KGT</small>
                                </div>
                                <div className="operation">
                                    <span>操作方式:</span>
                                    <span>充值</span>
                                </div>
                            </div>
                            <div>
                                <div className="num">
                                    <span>交易额:</span>
                                    <span>1234.56</span>
                                    <small>CNY</small>
                                </div>
                                <div className="status">
                                    <span>状态:</span>
                                    <span>{tradeStatus}</span>
                                </div>
                            </div>
                        </div>
                        <div className="btn_wrap">
                            <div className="channel">取消订单</div>
                            <div className="sure">确认收款</div>
                        </div>
                    </div>
                </Modal>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    shopList: state.shopList,
    coinDetail: state.marketCoin.coinDetail,
    loginData: state.loginData
});

export default withRouter(connect(
    mapStateToProps
)(UserRecharge))


